.loading {
    width: 100%;
    height: 100vh;
    // display: inline-block;
    -webkit-transition: 0.3s color, 0.3s border;
    transition: 0.3s color, 0.3s border;
    text-align: center;
    color: #d60b52;
    font-size: 30px;
    line-height: 100%;
    position: absolute;
    z-index: 100;
    top: 0;
    left: 0;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

