.loader-18:before,
.loader-18:after {
    content: '';
    display: block;
    position: absolute;
    border-radius: 50%;
    border: 0.1em solid transparent;
    border-bottom-color: currentcolor;
    top: 0;
    left: 0;
    -webkit-animation: 1s loader-18 linear infinite;
    animation: 1s loader-18 linear infinite;
}
.loader-18:before {
    width: 1em;
    height: 1em;
}
.loader-18:after {
    width: 0.8em;
    height: 0.8em;
    top: 0.1em;
    left: 0.1em;
    -webkit-animation-direction: reverse;
    animation-direction: reverse;
}
@-webkit-keyframes loader-18 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes loader-18 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
.loader-19 {
    border-top: 0.2em solid currentcolor;
    border-right: 0.2em solid transparent;
    -webkit-animation: loader-19 1s linear infinite;
    animation: loader-19 1s linear infinite;
    border-radius: 100%;
    position: relative;
}
@-webkit-keyframes loader-19 {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes loader-19 {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
.loader-20 {
    border: 0.2em dashed currentcolor;
    position: relative;
    border-radius: 5px;
    -webkit-animation: 1s loader-20 linear infinite;
    animation: 1s loader-20 linear infinite;
}
.loader-20::after {
    content: '';
    display: block;
    background: currentcolor;
    width: 0.3em;
    height: 0.3em;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
@-webkit-keyframes loader-20 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(3660deg);
    }
}
@keyframes loader-20 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.loader-21 {
    position: relative;
    width: 30px;
    height: 30px;
    border: 2px solid currentcolor;
    border-top-color: rgba(0, 0, 0, 0.2);
    border-right-color: rgba(0, 0, 0, 0.2);
    border-bottom-color: rgba(0, 0, 0, 0.2);
    border-radius: 100%;
    animation: loader-21 infinite 0.75s linear;
}

@keyframes loader-21 {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}

.loader-22 {
    position: relative;
    width: 1em;
    height: 1em;
    border: 0.15em solid currentcolor;
    border-top-color: transparent;
    border-bottom-color: transparent;
    border-radius: 100%;
    animation: loader-22 infinite 0.75s linear;
}

.loader-22:before,
.loader-22:after {
    position: absolute;
    top: 0.6em;
    left: -0.1em;
    border-top: 0.2em solid currentcolor;
    border-right: 0.2em solid transparent;
    border-left: 0.2em solid transparent;
    content: '';
    transform: rotate(-45deg);
}
.loader-22:after {
    top: -0.12em;
    left: 0.38em;

    transform: rotate(135deg);
}

@keyframes loader-22 {
    0% {
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(0);
    }
}

.loader-23 {
    position: relative;
    width: 1.5em;
    height: 1.5em;
}

.loader-23:before,
.loader-23:after {
    position: absolute;
    width: 0.5em;
    height: 0.5em;
    content: '';
    border-radius: 100%;
    left: -0.25em;
    background-color: currentcolor;
}

.loader-23:before {
    transform: translate(0, 0);
    animation: loader-23-before infinite 1.5s linear;
}

.loader-23:after {
    transform: translate(1.5em, 1.5em);
    animation: loader-23-after infinite 1.5s linear;
}

@keyframes loader-23-after {
    0% {
        transform: translate(1.5em, 1.5em);
    }

    25% {
        transform: translate(0, 1.5em);
    }

    50% {
        transform: translate(0, 0);
    }

    75% {
        transform: translate(1.5em, 0);
    }

    100% {
        transform: translate(1.5em, 1.5em);
    }
}

@keyframes loader-23-before {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(1.5em, 0);
    }

    50% {
        transform: translate(1.5em, 1.5em);
    }

    75% {
        transform: translate(0, 1.5em);
    }

    100% {
        transform: translate(0, 0);
    }
}

.loader-24 {
    margin: 0.7em;
    position: relative;
    width: 0.01em;
    height: 0.01em;
}

.loader-24:before,
.loader-24:after {
    position: absolute;
    display: inline-block;
    width: 0.5em;
    height: 0.5em;
    content: '';
    border-radius: 100%;
    background-color: currentcolor;
}

.loader-24:before {
    left: -0.5em;
    animation: loader-24 infinite 0.75s -0.4s cubic-bezier(0.2, 0.68, 0.18, 1.08);
}

.loader-24:after {
    right: -0.5em;
    animation: loader-24 infinite 0.75s cubic-bezier(0.2, 0.68, 0.18, 1.08);
}

@keyframes loader-24 {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    50% {
        transform: scale(0.1);
        opacity: 0.6;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.loader-25 {
    position: relative;
    width: 1.5em;
    perspective: 7em;
}

.loader-25:before,
.loader-25:after {
    position: absolute;
    width: 0.5em;
    height: 0.5em;
    content: '';
    animation: loader-25 0.5s infinite alternate;
    background: currentcolor;
    border-radius: 50%;
}

.loader-25:before {
    left: 0;
}

.loader-25:after {
    right: 0;
    animation-delay: 0.15s;
}

@keyframes loader-25 {
    0% {
        transform: scale(1) translateY(0px) rotateX(0deg);
        box-shadow: 0 0 0 currentcolor;
    }

    100% {
        transform: scale(1.2) translateY(-25px) rotateX(45deg);
        background: currentcolor;
        box-shadow: 0 0.8em 1.5em currentcolor;
    }
}

.loader-26 {
    position: relative;
    width: 1.51em;
    height: 1.51em;
    animation: loader-26 3s infinite linear;
    border: 0.025em solid currentColor;
    border-radius: 100%;
}

.loader-26:before,
.loader-26:after {
    position: absolute;
    left: 1px;
    top: 1px;
    width: 0.4em;
    height: 0.4em;
    content: '';
    border-radius: 100%;
    background-color: currentColor;
    box-shadow: 0 0 0.35em currentColor;
}

.loader-26:after {
    right: 0;
    width: 0.7em;
    height: 0.7em;
    margin: 0.4em;
}

@keyframes loader-26 {
    from {
        transform: rotate(0) translateZ(0);
    }

    to {
        transform: rotate(360deg) translateZ(0);
    }
}

.loader-27 {
    width: 1em;
    height: 1em;
    display: inline-block;
    padding: 0px;
    border-radius: 100%;
    border: 0.2em solid;
    border-top-color: rgba(254, 168, 23, 0.65);
    border-bottom-color: rgba(57, 154, 219, 0.65);
    border-left-color: rgba(188, 84, 93, 0.95);
    border-right-color: rgba(137, 188, 79, 0.95);
    animation: loader-27 2s ease-in-out infinite alternate;
  }
  
  @keyframes loader-27 {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(720deg);
    }
  }


  .loader-28 {
    display: inline-block;
    width: 0;
    height: 0;
    border-left: 0.4em solid transparent;
    border-right: 0.4em solid transparent;
    border-bottom: 0.4em solid #4183d7;
    border-top: 0.4em solid #f5ab35;
    animation: loader-28 1.2s ease-in-out infinite alternate;
  }
  
  @keyframes loader-28 {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(720deg);
    }
  }

  .loader-29 {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-left: 2px solid transparent;
    border-right: 2px solid transparent;
    border-bottom: 2px solid #000;
    border-top: 2px solid #000;
    animation: loader-29 1.8s ease-in-out infinite alternate;
  }
  
  .loader-29:before {
    content: " ";
    position: absolute;
    z-index: -1;
    top: 0.2em;
    left: 0px;
    right: 0px;
    bottom: 0.2em;
    border-left: 0.1em solid #000;
    border-right: 0.1em solid #000;
  }
  
  @keyframes loader-29 {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(720deg);
    }
  }




  